// Mobile Menu 
const mobileSlide = () => {
  const mobileMenu = document.getElementById("mobile-menu");
  const nav = document.getElementById("nav-items");
  const navLinks = document.querySelectorAll("#nav-items li");
  const headerNav = document.querySelector(".main-nav");


  mobileMenu.addEventListener("click", () => {
    // Toggle Nav
    mobileMenu.classList.toggle("toggle");
    nav.classList.toggle("mobile-active");

    // Animate Links
    navLinks.forEach((link, index) => {
      if(link.style.animation) {
        link.style.animation = "";
      } else {
        link.style.animation = `navLinkFade 0.5s ease ${index / 7 + 0.5}s forwards`;
      }
    }); 

// Close menu when clicking outside
  document.addEventListener('click', function(event) {
  if (event.target !== headerNav && !headerNav.contains(event.target)) {
      // hide the menu
    nav.classList.remove("mobile-active");
    mobileMenu.classList.remove("toggle");
  }

  document.onclick = function(e) {
    if(e.target !== mobileMenu && e.target !== navLinks) 
  {
    mobileMenu.classList.remove('toggle');
    nav.classList.remove('mobile-active');
  } 
  }
});
  });
}
mobileSlide();
// End Mobile Menu

// EmailJS
function validate() {
  let name = document.querySelector(".name");
  let email = document.querySelector(".email");
  let msg = document.querySelector(".message");
  let btn = document.querySelector(".submit");
  const re =
    /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

  btn.addEventListener("click", (e) => {
    e.preventDefault();
    if (name.value == "" || msg.value == "") {
      emptyerror();
    } else if (!(re.test(email.value.trim()))) {
      error();  
    } else {
      sendmail(name.value, email.value, msg.value);
      success();
      name.value = "";
      email.value = "";
      msg.value = "";
    }
  });
}
validate();

function sendmail(name, email, msg) {
  emailjs.send("service_9kew26n", "template_hfolbss", {
    to_name: name,
    from_name: email,
    message: msg,
  });
}

function emptyerror() {
  Swal.fire({
    icon: "error",
    title: "Oops...",
    text: "Molimo popunite sva polja!",
  });
}

function error() {
  Swal.fire({
    icon: "error",
    title: "Oops...",
    text: "Molimo Vas unesite ispravnu Email adresu!",
  });
}

function success() {
  Swal.fire({
    icon: "success",
    title: "Poslano!",
    text: "Poruka je uspje≈°no poslana",
  });
}
// End EmailJS